@use "../../styles/partials/" as *;

.ticketListContainer {
    display: flex;
    flex: 1;
    gap: map-get($map: $scale, $key: 16);
    overflow-x: scroll;
    background-color: map-get($map: $light-color, $key: light-400);
}
.ticketListContainer::-webkit-scrollbar {
  display: none;
}
